import React from 'react';
import './cart-input.scss';

const CartInput = ({ placeholder, type, key, value, onChange }) => (
  <div className="cart-input">
    <input
      onChange={onChange}
      value={value}
      id={key}
      type={type}
      required
      className="cart-input__input"
    />
    <label htmlFor={key} className="cart-input__label">
      {placeholder}
    </label>
  </div>
);

export default CartInput;
