import React from 'react';
import './cart-textarea.scss';

const CartTextarea = ({ rows, id, placeholder }) => (
  <div className="cart-textarea">
    <textarea required id={id} rows={rows} className="cart-textarea__input" />
    <label htmlFor={id} className="cart-textarea__label">{placeholder}</label>
  </div>
);

export default CartTextarea;
