import React, { Component } from 'react';
import './cart-order.scss';
import CartInput from '../cart-input/CartInput';
import CartCheckbox from '../cart-checkbox/CartCheckbox';
import CartButton from '../cart-button/CartButton';

class CartOrder extends Component {
  changeFio = e => {
    const { onChange } = this.props;
    onChange('fio', e.target.value);
  };
  changeEmail = e => {
    const { onChange } = this.props;
    onChange('email', e.target.value);
  };
  changePhone = e => {
    const { onChange } = this.props;
    onChange('phone', e.target.value);
  };
  render() {
    const {
      data,
      withDelivery,
      withPayment,
      togglePayment,
      toggleDelivery,
      goToNextTab
    } = this.props;
    return (
      <div className="cart-order">
        <form className="cart-order__form">
          <CartInput
            value={data.fio}
            onChange={this.changeFio}
            key="fio"
            placeholder="ФИО"
            type="text"
          />
          <CartInput
            value={data.email}
            onChange={this.changeEmail}
            key="email"
            placeholder="Email"
            type="text"
          />
          <CartInput
            value={data.phone}
            onChange={this.changePhone}
            key="phone"
            placeholder="Телефон"
            type="text"
          />
          <CartCheckbox
            onChange={toggleDelivery}
            checked={!withDelivery}
            text="Самовывоз"
          />
          <CartCheckbox
            onChange={togglePayment}
            checked={!withPayment}
            text="Оплата при получении"
          />
        </form>
        {(withDelivery || withPayment) && (
          <div className="cart-order__control">
            <CartButton onClick={goToNextTab} type="simple" value="Далее" />
          </div>
        )}
      </div>
    );
  }
}

export default CartOrder;
