import React, { useState } from 'react';
import './cart.scss';
import CartBlock from './cart_components/cart-block/CartBlock';

const Cart = ({data}) => {
  const [isOpen, setOpen] = useState(false);
  const openCart = () => setOpen(true);
  const closeCart = () => setOpen(false);

  return (
    <>
      <button
        onClick={openCart}
        className="cart__btn"
      >
        Cart
      </button>
      {isOpen && (
        <div className="cart__container">
          <div
            onClick={closeCart}
            className="cart__container__overlay"
          />
          <CartBlock items={data} />
        </div>
      )}
    </>
  );
};

export default Cart;
